          noticelists:[{
            "noticeid":{"val":"noticeid","desc":""},  //公告id
            "title": {"val":"【新工单1】","desc":""},  //标题
            "content":{"val":"[非常紧急]负一楼机房1#冷却塔有异常声音，请立即检查维修 。[1#冷却塔]","desc":""},  //内容
            "createby":{"val":"hcj","desc":"黄长江"}, //发布人
            "startdate":{"val":"1478679705947","desc":""}, //发布时间
            "isnotice":{"val":"1","desc":""} //是否为公告，1为公告，2为活动
          },
            {
              "noticeid":{"val":"noticeid","desc":""},  //公告id
